<template>
  <el-container class="answer-el-container">
    <el-header class="answer-el-header">
      <el-page-header @back="goBack" content="案例分析"/>
    </el-header>
    <el-main class="answer-el-main">

      <el-container>
        <el-header>
          <exam-head 
          @btnClick="start" 
          ref="answerHead" 
          style="float:left;">
            <span slot="btnName">开始考试</span>
          </exam-head>
          <el-button 
          round
          @click="analysisClick" 
          style="float:left;margin-left:20px">
            <span>题目解析</span>
          </el-button>
        </el-header>
        <el-container>
          <el-main>
              
            <div class="answer-img-box" v-for="(item, index) in currentAnswerQuestion" :key="index">
              <img :src="`${publicPath}` + item.answerQuestionSrc" :alt="item.answerQuestionSrc"><br/>
              <img :src="`${publicPath}` + item.analysis" v-show="isShow"><br/>
            </div>

          </el-main>
            <el-input
              type="textarea"
              :autosize="{ minRows: 10, maxRows: 20}"
              placeholder="请输入内容"
              v-model="draft"
              style="width:40vw;"
              v-show="this.currentAnswerQuestion.length !== 0">
            </el-input>
            
        </el-container>
      </el-container>


    </el-main>
  </el-container>
</template>

<script>
  import ExamHead from './childComps/ExamHead'
export default {
  name: 'MainAnswer',
  data() {
    return {
      currentOption: '',
      currentAnswerQuestion: [],
      publicPath: process.env.BASE_URL,
      draft: '',
      isShow: false
    }
  },
  props: {
    cquestionInfo: {
      type: Object,
      default() {
        return {}
      }
    }
  },
  components: {
    ExamHead
  },
  methods: {
    goBack() {
      this.$router.go(-1)
    },
    start() {
      this.resetValue()
      this.currentOption = this.$refs.answerHead.optionValue[0] + this.$refs.answerHead.optionValue[1]
      this.getQuestionInfo()
    },
    getQuestionInfo() {
      for(let i of this.cquestionInfo.paperInfo) {
        if(i.courseName === this.currentOption) {
          this.currentAnswerQuestion = i.answerQuestion
        }
      }
    },
    analysisClick() {
      this.isShow = !this.isShow
      this.$message({showClose: true, message: '提示: 解析显示在题目下方', type: 'warning'})
    },
    resetValue() {
      this.currentOption = ''
      this.currentAnswerQuestion = []
    }
  }
}
</script>

<style>
  .answer-el-main {
    height: 72vh;
    margin: 0 32px;
  }
  .answer-img-box {
    height: 60vh;
  }
  .answer-img-box img {
    width: 600px;
  }
  .answer-starexam-button {
    margin-left: 20px;
  }
</style>